$background-color: black;
$todoitem-back-color: #fff;
$todoitem-color: black;
$link-color: aqua;
$calendartileback-color: linear-gradient(to bottom right, #00005525, #0000009f);
$calendartile-color: white;
$inputback-color: linear-gradient(grey, white, grey);

@import url('https://fonts.googleapis.com/css2?family=Yellowtail&display=swap');
@import url("https://fonts.googleapis.com/css2?family=Squada+One&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Castoro&display=swap");


body {
  height: 100%;
  font-family: "Squada One";
  font-size: 1.2em;
  line-height: 1.4;
  background: $background-color;
  background-size: 9000px;
  background-position: center; }

a {
  color: darkcyan;
  text-decoration: none; }

.container {
  padding: 0 1rem; }

.btn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
  font-family: "Squada One";
  font-size: 20px; }

.btn:hover {
  background: #666; }

.deleteBtn {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 10px;
  cursor: pointer;
  margin-left: 46.5%;
  margin-bottom: 1%;
  font-family: "Squada One";
  font-size: 20px;
  border-radius: 5px; }

.deleteBtn:disabled {
  background-color: #eee;
  color: grey;
  border: solid 1px red; }

.deleteBtn:disabled:hover {
  background-color: #eee;
  color: grey; }

.deleteBtn:hover {
  background: #666; }

.headerLink {
  color: $link-color;
  font-size: large; }

p {
  color: #fff; }

.todoItem {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  background-color: $todoitem-back-color;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px; }

.todoItem p {
  color: $todoitem-color; }

.todosPlaceholder {
  text-align: center;
  font-size: 2em;
  color: white;
  font-family: "Castoro"; }

.calendar {
  text-shadow:  1px 1px 10px black, 0 0 3px white;
  color: white;
  text-align: center;
  text-decoration: none;
  max-width: 1000px;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.500em;
  margin: auto; }

.calTile {
  text-shadow:  1px 1px 10px black, 0 0 3px white;
  background-color: $link-color;
  padding: 5px;
  border: 1px solid black;
  color: $calendartile-color;
  background: $calendartileback-color;
  text-align: center;
  justify-content: center; }

video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
  margin-left: -4%; }

.title {
  font-family: "Yellowtail", Arial;
  font-size: 52pt;
  background: -webkit-linear-gradient(#eee, black);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent; }

.inputFields {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  margin-bottom: 20px; }

.checkbox {
  flex: 25%;
  margin-top: 1%; }

.todoTask {
  flex: 80%;
  margin-top: 1%; }

.todoPriority {
  flex: 80%;
  margin-top: 1%; }

.todoDuedate {
  flex: 100%;
  margin-bottom: 1%; }

.todoButton {
  flex: 20%;
  margin-top: 1%; }

.inputTask {
  flex: 100%;
  padding: 5px; }

.inputPriority {
  flex: 33%;
  padding: 5px; }

.inputDuedate {
  flex: 33%;
  padding: 5px; }

.sortContainer {
  color: white;
  text-align: center;
  margin-bottom: 1%; }

.sortBtn {
  display: inline-block;
  border: none;
  background: white;
  color: black;
  padding: 5px 10px;
  cursor: pointer;
  font-family: "Squada One";
  font-size: 20px;
  margin-left: 5px; }

.sortBtn:hover {
  background: #666; }

.searchInput {
  margin-left: 23%;
  width: 8%;
  transition: width 0.4s ease-in-out; }

.searchInput:focus {
  width: 15%; }

@media only screen and (min-width: 600px) {

  video {
    margin-left: -3%; } }


@media only screen and (min-width: 768px) {

  video {
    margin-left: -2%; }

  .calTile {
    padding: 8px; }

  .addTodosContainer {
    max-width: 1010px;
    margin: auto; }

  .todoItem {
    max-width: 990px;
    margin: auto;
    margin-bottom: 10px; }

  .inputTask {
    flex: 10; }

  .inputPriority {
    flex: 5; }

  .inputDuedate {
    flex: 5; }

  .todoItem {
    text-align: initial; }

  .checkbox {
    flex: none; }

  .todoTask {
    flex: 8.2;
    padding-top: 10px; }

  .todoPriority {
    flex: 4.6;
    padding-top: 12px; }

  .todoDuedate {
    flex: 5;
    padding-top: 12px; }

  .todoButton {
    flex: 1; }

  .inputFields {
    text-align: initial; }

  .btn {
    flex: 1; } }

@media only screen and (min-width: 1200px) {
  video {
    margin-left: -1%; } }

@media only screen and (max-width: 600px) {

  .deleteBtn {
    margin-left: 30%; }

  .searchInput {
    margin-left: 30%;
    width: 37%;
    transition: width 0.4s ease-in-out; }

  .searchInput:focus {
    width: 40%; } }

